<!--
 * @Description: 修改账号信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-09-27 16:13:34
 -->
<template>
  <div>
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="600px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on"> + 创建管理/代理人员账号</v-btn>
        </template>-->
        <v-card>
          <v-card-title>
            <span class="headline">修改账号信息</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field
                    v-model="userData.user_name"
                    disabled
                    label="*管理/代理人员登录账号"
                    maxlength="16"
                    type="text"
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="userData.password"
                    label="*登录密码(修改将会覆盖之前的密码)"
                    type="text"
                    maxlength="26"
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="userData.true_name"
                    label="*管理/代理人员登录真实姓名"
                    type="text"
                    maxlength="16"
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="userData.email"
                    label="*邮箱"
                    type="text"
                    maxlength="36"
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field
                    v-model="userData.phone"
                    label="*电话"
                    type="text"
                    maxlength="11"
                    required
                  ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-select v-model="userData.gender" :items="['男', '女']" label="*选择性别" required></v-select>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-select v-model="userData.role_id" :items="rolesList" label="*选择一个角色" required></v-select>
                </v-flex>
              </v-layout>
              <!-- <small>*该操作是创建一个代理或者一个内部管理员工的登录账号，请谨慎分配账号的权限!</small> -->
              <!-- <p class="root_p" > 登录账号 <v-chip>{{ data.user_name }}</v-chip> </p>
              <p class="root_p" > 登录密码 <v-chip> ******** </v-chip> </p>
              <p class="root_p" > 真实姓名 <v-chip>{{ data.true_name }}</v-chip> </p>
              <p class="root_p" > 性别 <v-chip>{{ data.gender==1?"男":"女"  }}</v-chip> </p>
              <p class="root_p" > 角色 <v-chip>{{ data.role_name }}</v-chip> </p>
              <p class="root_p" > 手机号码 <v-chip>{{ data.phone }}</v-chip> </p>
              <p class="root_p" > 邮箱 <v-chip>{{ data.email }}</v-chip> </p>
              <p class="root_p" > 创建时间 <v-chip>{{ data.create_time | time }}</v-chip> </p>
              <p class="root_p" > 修改时间 <v-chip>{{ data.update_time | time }}</v-chip> </p>-->
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn>
            <v-btn color="info darken-1" @click="modifyMessage">确认修改</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>


  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      rolesList: [],
      rolesData: [],
      userData: {
        email: "",
        gender: "",
        id: "",
        phone: "",
        role_id: "",
        true_name: "",
        user_name: "",
        password: ""
      },
    };
  },
  // 更新完成
  mounted: function() {
    this.roleList();
  },
  methods: {
    // 角色列表
    roleList() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_query_role_list,
        data: {}
      })
        .then(response => {
          if (response.data.result === 0) {
            response.data.datas.forEach(item => {
              this.rolesList.push(item.role_name);
            });
            this.rolesData = response.data.datas;
          } else {
             this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
           this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    },
    dialogShow(data) {

      this.dialog = data.state;
    //   this.userData = data.list;

        this.userData.email = data.list.email,
        this.userData.gender = data.list.gender,
        this.userData.id = data.list.id,
       this.userData.phone = data.list.phone,
       this.userData.role_id = data.list.role_id,
        this.userData.true_name = data.list.true_name,
        this.userData.user_name = data.list.user_name,
        this.userData.password = data.list.password,


      this.userData.gender == 1
        ? (this.userData.gender = "男")
        : (this.userData.gender = "女");
      this.rolesData.forEach(item => {
        if (this.userData.role_id == item.role_id) {
          this.userData.role_id = item.role_name;
          return false;
        }
      });
    },
    // 修改提交 manages_modify_manage
    modifyMessage() {
      if (this.userData.true_name.length < 2) {
         this.$Dialog.getProductCheckResult_({text: "请输入真实姓名（2-16）个字符",timeout: 3000, snackbar: true });
        return false;
      }
      if (this.userData.email.length < 5) {
         this.$Dialog.getProductCheckResult_({text: "请输入正确的邮箱地址",timeout: 3000, snackbar: true });
        return false;
      }
      if (this.userData.phone.length !== 11) {
         this.$Dialog.getProductCheckResult_({text: "请输入正确的手机号码",timeout: 3000, snackbar: true });
        return false;
      }
      if (this.userData.gender.length == "") {
         this.$Dialog.getProductCheckResult_({text: "请选择管理/代理人员性别",timeout: 3000, snackbar: true });
        return false;
      }
      if (this.userData.role_id.length == "") {
         this.$Dialog.getProductCheckResult_({text: "请选择一个权限角色",timeout: 3000, snackbar: true });
        return false;
      }
      // 解析角色ID
      this.rolesData.forEach(item => {
        if (this.userData.role_id == item.role_name) {
          this.userData.role_id = item.role_id;
          return false;
        }
      });
      this.userData.gender == "男"
        ? (this.userData.gender = 1)
        : (this.userData.gender = 1);

      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_modify_manage,
        data: this.userData
      })
        .then(response => {
          if (response.data.result === 0) {
             this.$Dialog.getProductCheckResult_({text: "账号信息已修改，操作完成！",timeout: 3000, snackbar: true });
            this.dialog = false;
            this.userData.gender = "";
            this.userData.role_id = "";
            this.$parent.$parent.info();
          } else {
             this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
           this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less" scoped>
.root_p {
  font-size: 16px;
}
</style>
