<!--
 * @Description: 创建管理/代理人员账号弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-09-29 18:55:39
 -->
<template>
  <div >
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="600px">
        <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on"> + 创建管理/代理人员账号</v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline">创建管理/代理人员账号</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                
                <v-flex xs12>
                  <v-text-field v-model="userData.user_name" label="*管理/代理人员登录账号" maxlength="16" type="text" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="userData.password" label="*登录密码" type="text" maxlength="26" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="userData.true_name" label="*管理/代理人员登录真实姓名" type="text" maxlength="16" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="userData.email" label="*邮箱" type="text" maxlength="36" required></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="userData.phone" label="*电话" type="text" maxlength="11" required></v-text-field>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-select v-model="userData.gender" :items="['男', '女']" label="*选择性别" required></v-select>
                </v-flex>
                <v-flex xs12 sm6>
                  <v-select v-model="userData.role_id" :items="rolesList" label="*选择一个角色" required></v-select>
                </v-flex>
                
              </v-layout>
            </v-container>
            <small>*该操作是创建一个代理或者一个内部管理员工的登录账号，请谨慎分配账号的权限!</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn>
            <v-btn color="info darken-1" @click="addManages($event)">提交</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      rolesData:[],
      rolesList:[],
      userData:{
        user_name:'',
        password:'',
        gender:'',
        email:'',
        phone:'',
        role_id:'',
        true_name:''
      }
    };
  },
  // 更新完成
  mounted: function() {
    this.roleList();
  },
  methods: {
    roleList() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_query_role_list,
        data: {}
      })
        .then(response => {
          //请求成功返回的数据

          if (response.data.result === 0) {
            
            response.data.datas.forEach(item => {
              this.rolesList.push(item.role_name);
            });
            this.rolesData = response.data.datas;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    },
      // 添加账号操作
      addManages(){
        if( this.userData.user_name.length < 5 ){ this.$Dialog.getProductCheckResult_({text: "😡" + "账号为字母/数字组合（5-16）位",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.password.length < 5 ){ this.$Dialog.getProductCheckResult_({text: "😡" + "密码为字母/数字组合（6-26）位",timeout: 3000, snackbar: true }); return false; }
         if( this.userData.true_name.length < 2 ){ this.$Dialog.getProductCheckResult_({text: "😡" + "请输入真实姓名（2-16）个字符",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.email.length < 5 ){ this.$Dialog.getProductCheckResult_({text: "😡" + "请输入正确的邮箱地址",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.phone.length !== 11 ){ this.$Dialog.getProductCheckResult_({text: "😡" + "请输入正确的手机号码",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.gender.length == "" ){ this.$Dialog.getProductCheckResult_({text: "😡" + "请选择管理/代理人员性别",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.role_id.length == "" ){ this.$Dialog.getProductCheckResult_({text: "😡" + "请选择一个权限角色",timeout: 3000, snackbar: true }); return false; }
        
        // 解析角色ID
        this.rolesData.forEach(item => {
          if( this.userData.role_id == item.role_name ){
            this.userData.role_id = item.role_id;
            return false;
          }
        });
        this.userData.gender=="男"?this.userData.gender = 1:this.userData.gender = 1;
        this.$axios({
        method: "post",
        url: this.$apis.URL.manages_add_manage,
        data: this.userData
      })
        .then(response => {
          //请求成功返回的数据
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: `已添加了新的账号${ this.userData.user_name }`,timeout: 3000, snackbar: true });
            this.$parent.$parent.info();
            this.dialog = false;
          } else {
            this.$Dialog.getProductCheckResult_({text: "😡" + response.data.message,timeout: 3000, snackbar: true });
          }
          this.userData.gender = "";
          this.userData.role_id = "";
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
      } 
  },
};
</script>

<style lang="less" scoped>
</style>
